<template>
  <div class="container mx-auto px-4 py-16 md:flex">
    <img class="max-w-full  h-96 md:w-1/3" :src="actorImage" alt="">
    <div class="md:ml-12 lg:ml-28 md:w-2/3">
      <h2 class="text-4xl font-semibold">{{ actor.name }}</h2>
      <div class="flex mt-2">
        <span class="mr-4">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 15.546c-.523 0-1.046.151-1.5.454a2.704 2.704 0 01-3 0 2.704 2.704 0 00-3 0 2.704 2.704 0 01-3 0 2.704 2.704 0 00-3 0 2.704 2.704 0 01-3 0 2.701 2.701 0 00-1.5-.454M9 6v2m3-2v2m3-2v2M9 3h.01M12 3h.01M15 3h.01M21 21v-7a2 2 0 00-2-2H5a2 2 0 00-2 2v7h18zm-3-9v-2a2 2 0 00-2-2H8a2 2 0 00-2 2v2h12z" />
          </svg>
        </span>
        <span class="text-gray-400">{{ actor.birthday }}</span>
      </div>
      <p class="mt-8">{{ actor.biography }}</p>
      <h4 class="font-bold mt-24 mb-8">Known For</h4>
      <div class="grid grid-cols-2 md:grid-cols-4 gap-4">
        <div v-for="know in knownfor" :key="know.id">
          <router-link :to="{ name: 'Movie', params: { id: know.id}}">
            <img :src="knowImage(know)" alt="">
            <div class="text-gray-400">{{ know.title }}</div>
          </router-link>
        </div>
      </div>
    </div>
  </div>


</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      socialDetails: [],
      actor: {},
      knownfor: []
    }
  },
  mounted() {
    this.fetchActor(this.$route.params.id)
    this.fetchCredits(this.$route.params.id)
  },
  methods: {
    async fetchActor(actorId){
      const response = await axios.get(`https://api.themoviedb.org/3/person/${actorId}?api_key=04c35731a5ee918f014970082a0088b1&language=en-US`)
      this.actor = response.data
      this.knownfor = response.data.cast
    },
    async fetchCredits(person_id){
      const res = await axios.get(`https://api.themoviedb.org/3/person/${person_id}/movie_credits?api_key=04c35731a5ee918f014970082a0088b1&language=en-US`)
      const filterNoImage = res.data.cast.filter(e => e.poster_path != null)
      this.knownfor = filterNoImage.slice(0, 4)
    },
    knowImage(know){
      if(know.poster_path){
        return "https://image.tmdb.org/t/p/w1280/" + know.poster_path
      } else {
        return "https://via.placeholder.com/300x450"
      }
    }
  },
  computed: {
    actorImage(){
      if(this.actor.profile_path){
        return "https://image.tmdb.org/t/p/w1280/" + this.actor.profile_path
      } else {
        return "https://via.placeholder.com/300x450"
      }
    },
    
  }
}
</script>